import qs from 'qs'

const apiMethods = {
  methods: {
    apiGet(url, data) {
      return new Promise((resolve, reject) => {
        axios.get(window.HOST + url, {
            params: data,
          },
        ).then((response) => {
          var that = this;
          if (response.data.code != 200) {
            that.$message.error(response.data.msg);
            if (response.data.code == -1) {
              that.$message.error(response.data.msg);
              this.$message({
                message: response.data.msg,
                type: 'error',
                duration: "1500",
                onClose: function () {
                  that.$router.push({
                    path: "/login"
                  })
                }
              });
            } else {
              that.$message.error(response.data.msg);
            }
          } else {

            // return response.data
            resolve(response)
          }
        }, (response) => {
          reject(response)
          this.$message.error(response.data.msg);
        })
      })
    },
    apiPost(url, data) {
      return new Promise((resolve, reject) => {
        axios.post(window.HOST + url, qs.stringify(data), {
          headers: {
            // "authorization": localStorage.getItem("token") ? localStorage.getItem("token") : ''
          }
        }).then((response) => {
          var that = this;
          if (response.data.code == 500) {
            that.$message.error(response.data.msg);
            return
          }
          if (response.data.code != 200) {
            that.$message.error(response.data.msg);
            if (response.data.code == 11001) {
              that.$router.push({
                path: "/login"
              })
            }
          } else if (response.data.code==200) {
            resolve(response)
          }
        }).catch((response) => {
          console.log('未进入')
          resolve(response)
          that.$message.error(response.data.msg);
        })
      })
    },
    handelResponse(res, cb, errCb) {
      if (res.data.code == 200) {
        // console.log(res.data)
        cb(res.data)
      } else {
        if (typeof errCb === 'function') {
          errCb()
        }
        this.handleError(res)
      }
    },
    handleError(res) {
      if (res.data.code) {
        that.$message.error(res.data.msg)
      } else {
        console.log('default error')
      }
    },
    reAjax(url, data) {
      return new Promise((resolve, reject) => {
        axios.post(window.HOST + url, {
          params: data
        }).then((response) => {
          resolve(response.data)
        }, (response) => {
          reject(response)
        })
      })
    }
  },
  computed: {
    showLoading() {
      return store.state.globalLoading
    }
  }
}

export default apiMethods



