<template>
	<script ref="editor" type="text/plain" :id="id"></script>
</template>
<script>

	/* eslint-disable */
	export default {
		name: "ueditor",
		data() {
			return {
				id: 'ueditorId' + Math.random().toString(16).substring(2),
				editor: null
			};
		},
		props: {
			defaultMsg: {
				type: String,
			},
			config: {
				type: Object,
				default: function() {
					return {
						autoHeightEnabled: false,
						autoFloatEnabled: true,
						initialContent: "",
						autoClearinitialContent: true,
						initialFrameWidth: null,
						initialFrameHeight: 450,
						BaseUrl: "",
						UEDITOR_HOME_URL: "html/static/ueditor/"
					}
				},
			}
		},
		watch: {},
		mounted() {
			const _this = this;
			_this.editor = UE.getEditor(_this.id, _this.config); // 初始化UE
			this.editor.addListener("ready", function() {
				setTimeout(function() {
					_this.editor.setContent(_this.defaultMsg); // 确保UE加载完成后，放入内容。
				}, 500)
			});
		},
		methods: {
			getUEContent() { // 获取内容方法
				return this.editor.getContent()
			},
			getUEContentTxt() { // 获取内容方法
				return this.editor.getContentTxt()
			},
			setContent(data) {
				const _this = this;
				_this.editor.addListener("ready", function() {
          _this.editor.setContent(data); // 确保UE加载完成后，放入内容。

         /* setTimeout(function() {
            _this.editor.setContent(data); // 确保UE加载完成后，放入内容。

            // _this.editor.setContent(_this.defaultMsg); // 确保UE加载完成后，放入内容。
          }, 500)*/
				});
			},
		},
		destroyed() {
			this.editor.destroy();
		},
	}
</script>



